.score-container {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
  padding: 10px;
}
.score-container .current-score,
.score-container .best-score {
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  color: white;
  padding: 10px;
  text-align: center;
  min-width: -moz-fit-content;
  min-width: fit-content;
  width: 200px;
}
.score-container .current-score {
  background-color: #3557b9;
}
.score-container .animate-scale-score {
  animation: scale-score 0.3s;
}
.score-container .best-score {
  background-color: #8f00ff;
}

@keyframes scale-score {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}/*# sourceMappingURL=score.css.map */