.cards-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 25px;
}

.cards-list.hide {
  display: none;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  gap: 15px;
  width: 200px;
  height: 225px;
  transition: 0.3s;
  animation: scale-cards 0.3s;
  background: linear-gradient(180deg, #00c6e7 0%, rgb(53, 87, 185) 147.62%);
  filter: drop-shadow(0px 8px 20px rgba(0, 0, 0, 0.25));
  border-radius: 10px;
}
.card:hover {
  transform: scale(1.07);
}
.card .character-image {
  width: 150px;
  height: 150px;
  -o-object-fit: contain;
     object-fit: contain;
  pointer-events: none;
}
.card .character-name {
  width: 100%;
  background-color: #3557b9;
  color: white;
  padding: 10px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  text-transform: capitalize;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@keyframes scale-cards {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@media (max-width: 1600px) {
  .card {
    width: 180px;
    height: 210px;
  }
  .card .character-image {
    width: 135px;
    height: 135px;
  }
}
@media (max-width: 1200px) {
  .card {
    width: 150px;
    height: 200px;
  }
  .card .character-image {
    width: 120px;
    height: 120px;
  }
}
@media (max-width: 480px) {
  .card {
    width: 130px;
    height: 200px;
  }
  .card .character-image {
    width: 120px;
    height: 120px;
  }
}/*# sourceMappingURL=cards.css.map */